import React, { useEffect, useState } from "react";
import Project from "./Project";

import axios from "axios";

const Projects = () => {
  const [projects, setProjects] = useState([]);

  useEffect(() => {
    const getProjects = async () => {
      const response = await axios("/project.json");
      const res_json = await response.data;

      setProjects(res_json);
    };
    getProjects();
  }, []);

  return (
    <section className="container section">
      <div className="u-padding-right">
        <h2 className="title">Proyectos realizados</h2>
        <p className="u-margin-txt" data-aos="fade-up" data-aos-duration="700">
          He realizado algunos proyectos para mejorar y afianzar mis
          conocimientos, te invito a ver mis repositorios en la plataforma
          GitHub. A continuaci√≥n te muestro una lista de algunos proyectos
          realizados.
        </p>
      </div>
      <div className="columns is-multiline">
        {projects.map((project) => (
          <Project key={project._id} project={project} />
        ))}
      </div>
    </section>
  );
};

export default Projects;
