/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import { useState } from "react";

const FormContact = () => {
  const [status, setStatus] = useState("");
  const submitForm = (ev) => {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        setStatus("SUCCESS");
      }
    };
    xhr.send(data);
  };
  return (
    <form
      action="https://formspree.io/xpzynper"
      method="POST"
      onSubmit={submitForm}
      className="form-contact"
    >
      <div className="field">
        <label className="label">Nombre</label>
        <div className="control">
          <input
            className="input"
            type="text"
            placeholder="Introduce t√∫ nombre"
            name="name"
          />
        </div>
      </div>

      <div className="field">
        <label className="label">Correo Electr√≥nico</label>
        <div className="control has-icons-left">
          <input
            className="input"
            type="email"
            name="_replyto"
            placeholder="Introduce t√∫ correo electr√≥nico"
          />
          <span className="icon is-small is-left">
            <i className="fas fa-envelope"></i>
          </span>
        </div>
      </div>

      <div className="field">
        <label className="label">Asunto</label>
        <div className="control">
          <input
            className="input"
            type="text"
            placeholder="Sobre que quieres hablar..."
          />
        </div>
      </div>

      <div className="field">
        <label className="label">Mensaje</label>
        <div className="control">
          <textarea
            className="textarea"
            placeholder="Escribe tu mensaje aqu√≠..."
            name="message"
          ></textarea>
        </div>
      </div>

      <div className="field">
        <div className="control">
          <button className="button">Enviar</button>
        </div>
      </div>
      {status === "SUCCESS" ? (
        <div className="field notification is-info is-light">
          <p>
            Gracias por tu mensaje{" "}
            <span role="img" aria-label="smille">
              üôÇ
            </span>{" "}
            me pondr√© en contacto contigo lo antes posible!!
          </p>
        </div>
      ) : null}
    </form>
  );
};

export default FormContact;
